<template>
  <div class="column is-3-desktop is-4-tablet is-full-mobile">
    <div class="box">
      <span><router-link :to="`/languages/${slug}`">{{ name }}</router-link></span>
      <br>
      <i v-for="country in countries" :key="country">{{ country }} </i>
      <br>
      <p v-if="translatedWordCount > 0">
        {{ translatedWordCount }} / {{ wordCount }} words translated
      </p>
      <p v-else>
        No translated words
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'language-item',
  props: {
    name: String,
    slug: String,
    countries: Array,
    translatedWordCount: Number,
    wordCount: Number,
  },
};
</script>

<style scoped>
a {
  font-size: 1rem;
  font-weight: 700;
  color: #3A4541;
}
i {
  font-style: normal;
}
.box {
  border-top: 4px solid #3A4541;
  background-color: #F1E685;
  margin-left: 0.75rem;
  margin-right: 0.75rem;
}
</style>



