<template>
  <div>
    <h1 class="title">Languages</h1>
    <div class="columns is-multiline">
      <language-item
        v-for="{ name, slug, countries } in languages"
        :key="slug"
        :name="name"
        :slug="slug"
        :countries="countries"
        :translatedWordCount="getTranslatedWordCount(slug)"
        :wordCount="wordCount"
      >
      </language-item>
    </div>
  </div>
</template>

<script>
import {
  getLanguages,
  getTranslatedWordCount,
  getWordCount,
} from '@/api';

import LanguageItem from '@/components/LanguageItem';

export default {
  name: 'languages',
  components: {
    LanguageItem,
  },
  data: () => ({
    languages: [],
    wordCount: 0,
  }),
  created() {
    this.languages = getLanguages();
    this.wordCount = getWordCount();
  },
  methods: {
    getTranslatedWordCount,
  },
};
</script>

<style scoped>

</style>



